.container {
  position: fixed;
  left: -300px;
  top: 50%;
  transform: translateY(-50%);
  transition: left 0.3s ease-in-out;
  height: fit-content;
  width: fit-content;
  z-index: 999;
}

.container.visible {
  left: 20px;
}

.blurred-background {
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 10px;
}

.row-container {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.row-nationality {
  display: flex;
  align-items: center;
}

.image-slot {
  width: 50px;
  height: 50px;
  background-color: rgb(190, 127, 196);
  margin-right: 10px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.text-entries {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
}

.cash-entry {
  margin-bottom: 5px;
  color: rgba(128, 128, 128, 0.774);
}

.age-entry {
  margin-bottom: 5px;
  color: rgba(128, 128, 128, 0.774);
}

.bank-entry {
  margin-bottom: 5px;
  color: rgba(128, 128, 128, 0.774);
}

.phone-entry {
  margin-bottom: 5px;
  color: rgba(128, 128, 128, 0.774);
}

.nationality-entry {
  margin-bottom: 5px;
  color: rgba(128, 128, 128, 0.774);
}

.text-entry-label {
  color: white;
  font-size: 20px;
  margin-bottom: 5px;
}